import { QuestionControl, TestTimer } from "components";
import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import { useMatch, useNavigate } from "react-router-dom";
import { useDatabase } from "repositories/database";

const TestPage: React.FC = () => {
  const database = useDatabase();
  const navigate = useNavigate();
  const match = useMatch("/test/:testNumber");
  const testNumber = Number(match?.params.testNumber);

  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [answeredUpTo, setAnsweredUpTo] = useState(0);
  const [answers, setAnswers] = useState([] as string[][]);
  const [showResult, setShowResult] = useState(false);
  const [success, setSuccess] = useState(false);
  const [score, setScore] = useState(0);

  const questions = database.tests.getTest(testNumber);

  const nextText =
    answers[questions.length - 1] === undefined ? "next" : "finish";

  const answered = (successInner: boolean, selectedOptions: string[]) => {
    setAnswers([...answers, selectedOptions]);
    setSuccess(successInner);
    setShowResult(true);
    if (successInner === true) setScore(score + 1);
  };

  const finish = () => {
    database.progress.record({
      testId: testNumber.toString(),
      takenOn: new Date(),
      correctAnsweredQuestions: score,
    });
    navigate(`/result/${testNumber}`);
  };

  const next = () => {
    if (nextText === "finish") {
      finish();
    } else {
      const offset = answers[answeredUpTo] === undefined ? 0 : 1;
      setShowResult(false);
      setAnsweredUpTo(answeredUpTo + offset);
      setCurrentQuestion(answeredUpTo + offset);
    }
  };

  const isAnsweredCorrect = (questionIndex: number) => {
    const expected = JSON.stringify(questions[questionIndex].answers.sort());
    const answeredArr = JSON.stringify(answers[questionIndex]?.sort());

    return expected === answeredArr;
  };

  const review = (questionIndex: number) => {
    setShowResult(answers[questionIndex] !== undefined);
    setSuccess(isAnsweredCorrect(questionIndex));
    setCurrentQuestion(questionIndex);
  };

  return (
    <article className="m-2">
      <h3 className="my-4 text-center">Test {testNumber}</h3>
      <h5>
        {currentQuestion + 1} from {questions.length}
      </h5>
      <div className="my-3">
        {`Test will end in `}
        <TestTimer totalSeconds={45 * 60} timesUp={finish} />
      </div>

      <QuestionControl
        question={questions[currentQuestion]}
        answered={answered}
        preselectedOptions={answers[currentQuestion]}
      />
      {showResult ? (
        success ? (
          <div className="my-4">
            <Button variant="success" onClick={next}>
              Correct, {nextText}
            </Button>
            <p>{questions[currentQuestion].description}</p>
          </div>
        ) : (
          <div className="my-4">
            <p>{questions[currentQuestion].description}</p>
            <Button variant="warning" onClick={next}>
              Incorrect, {nextText}
            </Button>
          </div>
        )
      ) : null}
      {
        <div className="my-3">
          {questions.map((_, questionIndex) => (
            <Button
              key={questionIndex}
              className="r-b-r mt-1"
              variant={
                currentQuestion === questionIndex
                  ? "primary"
                  : !isAnsweredCorrect(questionIndex) && answers[questionIndex]
                  ? "warning"
                  : "secondary"
              }
              disabled={answeredUpTo < questionIndex}
              onClick={() => review(questionIndex)}
            >
              {questionIndex + 1}
            </Button>
          ))}
        </div>
      }
    </article>
  );
};

export { TestPage };
