import React, { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Question } from "types";

const QuestionControl: React.FC<{
  question: Question;
  answered: (success: boolean, selectedOptions: string[]) => void;
  preselectedOptions?: string[];
}> = ({ question, answered, preselectedOptions }) => {
  const useCheckbox = question.answers.length > 1;

  const defaultState: Record<string, boolean> = {};
  preselectedOptions?.forEach((option) => {
    defaultState[option] = true;
  });

  const [state, setState] = useState(defaultState);
  const [hideCheckFor, setHideCheckFor] = useState([] as string[]);

  const defaultStateAsJson = JSON.stringify(defaultState);

  useEffect(
    () => setState(JSON.parse(defaultStateAsJson)),
    [defaultStateAsJson]
  );

  const check = () => {
    setHideCheckFor([...hideCheckFor, question.id]);

    const isCorrect =
      question.options
        .map(
          (option) =>
            (isCorrectOption(option) && isOptionSelected(option)) ||
            (!isCorrectOption(option) && !isOptionSelected(option))
        )
        .indexOf(false) < 0;

    const answers: string[] = [];

    for (const key in state) {
      if (Object.prototype.hasOwnProperty.call(state, key)) {
        if (state[key]) answers.push(key);
      }
    }

    answered(isCorrect, answers);
  };

  const select = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (useCheckbox) {
      setState({
        ...state,
        [event.target.id]: event.target.checked,
      });
    } else {
      setState({
        [event.target.id]: event.target.checked,
      });
    }
  };

  const isAnswered = () => hideCheckFor.indexOf(question.id) >= 0;
  const isOptionSelected = (option: string) => state[option] === true;
  const isCorrectOption = (option: string) =>
    question.answers.indexOf(option) >= 0;

  return (
    <article>
      <Form>
        <h3>{question.text}</h3>
        {question.options.map((option, index) => (
          <Form.Group key={index} className="mb-3" controlId={option}>
            <Form.Check
              type={useCheckbox ? "checkbox" : "radio"}
              label={option}
              onChange={select}
              checked={isOptionSelected(option)}
              disabled={isAnswered()}
              className={
                isAnswered()
                  ? isCorrectOption(option)
                    ? "text-success"
                    : isOptionSelected(option)
                    ? "text-danger"
                    : ""
                  : ""
              }
            />
          </Form.Group>
        ))}

        {hideCheckFor.indexOf(question.id) < 0 ? (
          <Button onClick={check}>Check</Button>
        ) : null}
      </Form>
    </article>
  );
};

export { QuestionControl };
