import React, { useState } from "react";
import ProgressBar from "react-bootstrap/ProgressBar";
import Button from "react-bootstrap/Button";
import { useMatch, useNavigate } from "react-router-dom";
import { useDatabase } from "repositories/database";

const TestResultPage: React.FC = () => {
  const database = useDatabase();
  const navigate = useNavigate();
  const match = useMatch("/result/:testNumber");
  const testNumber = Number(match?.params.testNumber);

  if (isNaN(testNumber)) navigate("/progress");

  const [state] = useState(database.progress.getCurrentStatus()[testNumber]);

  return (
    <article className="m-5">
      <h3 className="my-4 text-center">Test {testNumber}</h3>
      <h2 className="mb-2">
        {state.correctAnsweredQuestions} of{" "}
        {database.tests.getTest(testNumber).length}
      </h2>
      <ProgressBar
        title="Score"
        className="mb-5"
        variant={
          state.correctAnsweredQuestions < 15
            ? "danger"
            : state.correctAnsweredQuestions < 18
            ? "warning"
            : state.correctAnsweredQuestions < 20
            ? "info"
            : "success"
        }
        now={state.correctAnsweredQuestions}
        max={database.tests.getTest(testNumber).length}
        label={`${state.correctAnsweredQuestions}`}
      />
      {state.correctAnsweredQuestions < 18 ? (
        <>
          <h3 className="mb-5">You have failed, keep practicing.</h3>
          <Button href="progress">Check your progress</Button>
        </>
      ) : (
        <>
          <h3 className="mb-5">
            You have passed this test, but keep practicing.
          </h3>
          <Button href="progress">Check your progress</Button>
        </>
      )}
    </article>
  );
};

export { TestResultPage };
