import React from "react";
import { useDatabase } from "repositories/database";
import { Link } from "react-router-dom";
import ListGroup from "react-bootstrap/ListGroup";
import ListGroupItem from "react-bootstrap/ListGroupItem";
import Badge from "react-bootstrap/Badge";

const ProgressPage: React.FC = () => {
  const database = useDatabase();
  const status = database.progress.getCurrentStatus();
  const items = [];

  for (
    let testNumber = 1;
    testNumber <= database.tests.getTestsCount();
    testNumber++
  ) {
    const isTaken = !!status[testNumber];
    const { correctAnsweredQuestions, takenOn } = isTaken
      ? status[testNumber]
      : { correctAnsweredQuestions: 0, takenOn: undefined };

    items.push(
      <ListGroupItem
        key={testNumber}
        className="d-flex justify-content-between align-items-start"
        variant={
          !isTaken ? "" : correctAnsweredQuestions < 18 ? "danger" : "success"
        }
      >
        <div>
          <div className="fw-bold">
            Test {testNumber}{" "}
            <strong>
              {isTaken
                ? `(${correctAnsweredQuestions} of ${
                    database.tests.getTest(testNumber).length
                  })`
                : null}
            </strong>
          </div>
          <div className="text-secondary">
            {isTaken ? (
              <>
                {`Last test on ${takenOn?.toLocaleDateString()} `}
                <Link to={`/test/${testNumber}`}>Take</Link>
              </>
            ) : (
              <Link to={`/test/${testNumber}`}>Take</Link>
            )}
          </div>
        </div>
        {isTaken ? (
          <Badge pill>{correctAnsweredQuestions < 18 ? "" : ""}</Badge>
        ) : null}
      </ListGroupItem>
    );
  }

  return (
    <article className="my-5">
      <ListGroup>{items}</ListGroup>
    </article>
  );
};

export { ProgressPage };
