import { TestResult } from "types"

export class ProgressRepository {
  private status: Record<string, TestResult> = {};

  constructor(private storage: Storage) {
    const json = storage.getItem("progress");
    if (json) {
      this.status = JSON.parse(json);

      for (const key in this.status) {
        if (Object.prototype.hasOwnProperty.call(this.status, key)) {
          const element = this.status[key];
          element.takenOn = new Date(element.takenOn);
        }
      }
    }
  }

  record(result: TestResult) {
    this.status[result.testId] = result;
    this.storage.setItem("progress", JSON.stringify(this.status));
  }

  getCurrentStatus() {
    return { ...this.status };
  }
}
