import { IQuestionRepository, ITestRepository } from "abstractions";

export class TestRepository implements ITestRepository {

  constructor(
    private settings: string[][],
    private questions: IQuestionRepository,
  ) { }

  getTestsCount(): number {
    return this.settings.length;
  }

  getTest(testNumber: number) {
    if (testNumber < 1 || testNumber > this.settings.length)
      throw Error("Test number out of range.");

    return this.settings[testNumber - 1].map(id => this.questions.get(id));
  }
}