import { IQuestionRepository } from "abstractions";
import { Question } from "types";

export class QuestionRepository implements IQuestionRepository {
  constructor(private questions: Question[]) { }

  getAll(): Question[] {
    return this.questions
  };

  get(id: string): Question {
    const result = this.questions.find(x => x.id === id);

    if (result === undefined)
      throw Error("Question with this id does not exist.");

    return result;
  }
}
