import React from "react";
import {
  NotFoundPage,
  HomePage,
  TestPage,
  TestResultPage,
  ProgressPage,
} from "pages";
import { useRoutes } from "react-router-dom";

const AppRouter: React.FC = () => {
  const homePage = {
    path: "/",
    element: <HomePage />,
  };

  const testPage = {
    path: "/test/:testNumber",
    element: <TestPage />,
  };

  const resultPage = {
    path: "/result/:testNumber",
    element: <TestResultPage />,
  };

  const progressPage = {
    path: "progress",
    element: <ProgressPage />,
  };

  const notFound = {
    path: "*",
    element: <NotFoundPage />,
  };

  const routing = useRoutes([
    testPage,
    resultPage,
    progressPage,
    homePage,
    notFound,
  ]);

  return <>{routing}</>;
};

export { AppRouter };
