import React from "react";
import ThemeProvider from "react-bootstrap/ThemeProvider";
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Brand from "react-bootstrap/NavbarBrand";
import Toggle from "react-bootstrap/NavbarToggle";
import Collapse from "react-bootstrap/NavbarCollapse";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";
import { useDatabase } from "repositories/database";
import Badge from "react-bootstrap/Badge";

const AppLayout: React.FC = ({ children }) => {
  const database = useDatabase();

  const status = database.progress.getCurrentStatus();
  const tests = [];
  for (
    let testNumber = 1;
    testNumber <= database.tests.getTestsCount();
    testNumber++
  ) {
    tests.push(
      <NavDropdown.Item
        key={testNumber}
        href={`test/${testNumber}`}
        className="d-flex justify-content-between align-items-start"
      >
        Test {testNumber}
        <Badge pill>
          {(status[testNumber]?.correctAnsweredQuestions || 0) < 18 ? "" : ""}
        </Badge>
      </NavDropdown.Item>
    );
  }

  return (
    <ThemeProvider>
      <Navbar bg="dark" expand="sm" variant="dark">
        <Container>
          <Brand href=".">
            <img
              src="logo192.png"
              width="30"
              height="30"
              className="d-inline-block align-top"
              alt="logo"
            />{" "}
            Life in the UK
          </Brand>
          <Toggle aria-controls="basic-navbar-nav" />
          <Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="progress">Progress</Nav.Link>
              <NavDropdown title="Tests" id="basic-nav-dropdown">
                {tests}
              </NavDropdown>
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
      <Container>{children}</Container>
    </ThemeProvider>
  );
};

export { AppLayout };
